<template>
<div class="my-address">
    <van-nav-bar title="我的收货地址" left-text="返回" left-arrow @click-left="onClickLeft" />
	<div class="empty-box" v-if="list.length === 0">
        <van-empty class="custom-image" image="https://img.yzcdn.cn/vant/custom-empty-image.png" description="地址空空如也，添加一个吧~">
        </van-empty>
    </div>
    <van-address-list @edit="editAddress" @add="increaseAddress" :switchable="false" :list="list" default-tag-text="默认" />
</div>
</template>

<script>
import '../assets/scss/myaddress.scss'
import {
    mapGetters
} from 'vuex'

export default {
    data() {
        return {
            list: []

        }
	},
	created() {
		this.getAddressData();
	},
    methods: {
		
        getAddressData() {
			if(!this.getToken) {
				this.$router.push({
					name: 'Login'
				})

				return ;
			}

			let params = {
				appkey: this.appkey,
				tokenString: this.getToken
			}

			this.$toast({
				type: 'loading',
				message: '加载中',
				forbidClick: true,
				loadingType: 'spinner',
				duration: 0
			})

			this.axios({
				method: 'GET',
				url: '/findAddress',
				params
			})
			.then(res => {
				this.$toast.clear();
                console.log(res);
                if(res.data.code === 20000) {
					let arr = [];
                    res.data.result.forEach(item => {
						let province = item.province === item.city ? item.province : item.province + item.city;
						item.address = `${province}${item.county}${item.addressDetail}`
						
						// 默认地址排最前面
						if(item.isDefault === 1) {
							arr.unshift(item)
						}else {
							arr.push(item)
						}
                        
                    })
                    this.list = arr;
                }
			})
			.catch(err => {
				this.$toast.clear();
				console.log(err);
			})
        },
        onClickLeft() {
            this.$router.go(-1);
		},
		// 编辑地址
		editAddress(item) {
			console.log(item);
			this.$router.push({
				name: 'EditAddress',
				params: {
					aid: item.aid
				}
			})
		},
		// 新增地址
		increaseAddress() {
			this.$router.push({
				name: 'Address'
			})
		}
    },
    computed: {
        ...mapGetters(['getToken'])
    }
}
</script>
